<template>
  <div class="v-fullcalendar"/>
</template>

<script>
import { Calendar } from "@fullcalendar/core";

import { assert } from "./util";
import { isValidKey } from "./options";
import { validFullcalendarEvents } from "./events";

export default {
  name: "VFullcalendar",
  props: {
    config: {
      type: Object,
      required: true
    },
    events: {
      type: Array,
      required: true
    }
  },
  computed: {
    registerEvent() {
      return validFullcalendarEvents.filter(item =>
        this.$listeners.hasOwnProperty(item)
      );
    },
    registerEventHandle() {
      return this.registerEvent.reduce(
        (pre, next) => ({
          ...pre,
          [next]: (...args) => {
            this.$emit(next, ...args);
          }
        }),
        {}
      );
    }
  },
  watch: {
    events(newVal, oldVal) {
      if (newVal.every(item => item.id)) {
        const newIds = newVal.map(item => item.id);
        const oldIds = oldVal.map(item => item.id);
        const increased = newVal.filter(item => !oldIds.includes(item.id));
        const removed = oldVal.filter(oldItem => !newIds.includes(oldItem.id));

        if (removed.length) {
          removed.forEach(item => {
            this.calendar.getEventById(item.id).remove();
          });
        }

        if (increased.length) {
          increased.forEach(item => this.calendar.addEvent(item));
        }
      } else {
        this.calendar.getEvents().forEach(event => event.remove());
        newVal.forEach(item => this.calendar.addEvent(item));
      }
    },
    config() {
      this.calendar.destroy();
      this.instance();
    }
  },
  created() {
    this.validConfig();
  },
  mounted() {
    this.instance();
  },
  beforeDestroy() {
    this.calendar.destroy();
  },
  methods: {
    instance() {
      const calendar = new Calendar(this.$el, this.initConfig());
      calendar.render();

      this.calendar = calendar;
    },
    initConfig() {
      return {
        ...this.config,
        events: this.events,
        ...this.registerEventHandle
      };
    },
    getView() {
      return this.calendar.view;
    },
    gotoDate(date) {
      this.calendar.gotoDate(date);
    },
    validConfig() {
      Object.keys(this.config).forEach(key => {
        if (!isValidKey(key)) {
          assert(`your config has not verifiable literals ${key}`);
        }
      });
    }
  }
};
</script>
